/***** Article *****/
.article {
  /*
  * The article grid is defined this way to optimize readability:
  * Sidebar | Content | Free space
  * 17%     | 66%     | 17%
  */
  @include desktop {
    flex: 1 0 66%;
    max-width: 66%;
    min-width: 640px;
    padding: 0 30px;
  }

  flex: 1 0 auto; //Explicit values needed whith flex-direction: column for IE11
  padding: 0 1rem;
  font-size: .875rem;

  &-container {
    @include desktop {
      flex-direction: row;
    }

    display: flex;
    flex-direction: column;
  }

  &-header {
    @include tablet {
      flex-direction: row;
      margin-top: 0;
    }

    align-items: flex-start;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-between;
    //margin-bottom: 2rem;
    margin-top: 1rem;
  }

  &-avatar {
    margin-right: .8rem;
  }

  &-author {
    margin-bottom: .8rem;
  }

  &-title {
    @include tablet {
      flex-basis: 100%; /* Take entire row */
    }

    .icon-lock {
      position: relative;
      left: -5px;
      vertical-align: baseline;
    }
  }

  [role="button"] {
    @include tablet { width: auto; }
    flex-shrink: 0; /*Avoid collapsing elements in Safari (https://github.com/philipwalton/flexbugs#1-minimum-content-sizing-of-flex-items-not-honored)*/
    width: 100%;
  }

  &-info {
    max-width: 100%;
  }

  &-meta {
    display: inline-block;
    vertical-align: middle;
  }

  &-body {
    @include content-body;
    > p:last-child { margin-bottom: 0; }
  }

  &-content {
    line-height: 1.6;
    margin: 1rem 0;
    word-wrap: break-word;
  }

  &-footer {
    align-items: center;
    display: flex;
    justify-content: space-between;
    padding-bottom: 2rem;
  }

  &-comment-count {
    color: $secondary-text-color;
    font-weight: $font-weight-light;

    &:hover {
      text-decoration: none;
    }

    &-icon {
      vertical-align: middle;
      color: $brand_color;
      width: 18px;
      height: 18px;
    }
  }

  &-sidebar {
    @include desktop {
      border: 0;
      flex: 0 0 17%;
      height: auto;
    }

    //border-bottom: 1px solid $border-color;
    //border-top: 1px solid $border-color;
    flex: 1 0 auto; //Explicit values needed whith flex-direction: column for IE11
    padding: 0;
    background: $hue-interface;
    margin: 0 -1rem 1rem;
    border-radius: $border-radius $border-radius 0 0;
    height: 2.5rem;
  }

  &-relatives {
    @include tablet {
      flex-direction: row;
    }

    border-top: 1px solid $border-color;
    display: flex;
    flex-direction: column;
    padding: 20px 0;

    > * {
      flex: 1 0 50%; //Explicit values needed whith flex-direction: column for IE11
      min-width: 50%;
      overflow-wrap: break-word;
      margin-right: 0;

      &:last-child { padding: 0; }

      @include tablet { padding-right: 20px; }
    }
  }


  &-votes {
    border-top: 1px solid $border-color;
    padding: 3rem 0;
    text-align: center;
  }

  &-vote {
    margin: 1rem .5rem;
    min-width: 90px;
    width: auto;
  }

  &-more-questions {
    margin: 1rem 0 2rem;
    text-align: center;
  }

  &-return-to-top {
    @include desktop { display: none; }

    border-top: 1px solid $border-color;

    a {
      color: $text_color;
      display: block;
      padding: 20px 0;

      &:hover,
      &:focus {
        text-decoration: none;
      }
    }

    &-icon {
      transform: rotate(0.5turn);
    }
  }

  &-subscribe button {
    @extend .button;
    &[data-selected="true"] {
      @extend .button-primary;
    }
  }
}

.sidenav {
  &-title {
    font-size: $font-size-base;
    position: relative;
    font-weight: $font-bold;
    color: $hue-dark;
  }

  &-item {
    border-radius: 4px;
    color: $text_color;
    display: block;
    font-weight: $font-weight-light;
    margin-bottom: 1rem;
    padding: 1rem;

    &.current-article,
    &:hover {
      background-color: $brand_color;
      color: $brand_text_color;
      text-decoration: none;
    }
  }
}

.recent-articles,
.related-articles {
  &-title {
    @extend .comment-heading
  }

  li {
    margin-bottom: 15px;
  }
}
